  max-width: 100vw;
  object-fit: cover;
}

.fairBackground{
  position: absolute;
  z-index: -3;
  display: -webkit-flex;
  display: flex;
  /* bottom: 0.1vw;
  left: 19vw; */
  width: 100vw;
  
}

.objects {
